/* src/components/Nhanvat/NhanvatStats.module.css */
.statsContainer {
    width: 100%;
    /* Dng aspect-ratio  to khung vung nu h tr */
    aspect-ratio: 1 / 1;
    position: relative;
    overflow: visible;
    /* t min-height  m bo chiu cao > 0 trn mobile */
    min-height: 180px;
    /* Fallback nu browser khng h tr aspect-ratio */
    height: auto;
  }
  
  /* ResponsiveContainer wrapper: chim ton b vng ca statsContainer */
  .statsContainer > div {
    position: absolute !important;
    width: 100%;
    inset: 0 !important;
  }
  
  /* Cho php cc SVG v label khng b ct */
  .statsContainer svg,
  .statsContainer svg g {
    overflow: visible !important;
  }
  .statsContainer .recharts-polar-angle-axis tspan {
    white-space: nowrap;
  }
  
  /* Mobile-specific: tng min-height trn mn nh */
  @media (max-width: 600px) {
    .statsContainer {
      min-height: 150px;
    }
  }
  