/* Character Info section for NhanvatInfo.jsx */

.infoBlock {
  display: flex;
  flex-direction: column;
  gap: 6px;
  text-align: left;
  align-items: flex-start;
  width: 100%;
}

.nameRow {
  display: flex;
  align-items: center;
  gap: 10px;
  width: 100%;
  justify-content: center;
}

.name {
  font-size: 2.2rem;
  font-weight: bold;
  margin: 0;
  display: flex;
  align-items: center;
  gap: 10px;
  white-space: nowrap;
}

.genderIcon {
  font-size: 1.2rem;

}

.personalInfo {
  display: flex;
  flex-direction: column;
  gap: 6px;
  font-size: 0.95rem;
  margin-bottom: 6px;
  width: 100%;
  align-items: flex-start;
}

.infoItem {
  display: flex;
  gap: 6px;
  align-items: center;
  white-space: nowrap;
  justify-content: flex-start;
}

.infoItem strong {
  display: inline-block;
  width: 80px;
}

.fullDescription {
  flex: 1;
  padding: 0;
  margin: 0;
  border-radius: 8px;
  /* Tr v normal  dng margin ca <p> */
  white-space: normal;
  font-size: 0.95rem;
  /* Bn c th gim nh line-height nu mun */
  line-height: 1.4;      
  text-align: left;
  word-break: break-word;
}

/* Gim margin bottom ca <p> cho gn nhau hn */
.fullDescription p {
  margin: 0 0 0.1em;  /* trc y mc nh c th l 1em */
}

/* Cng m bo <br> khng to qu nhiu khong trng */
.fullDescription br {
  display: block;
  content: "";
  margin: 0;           /* xa margin mc nh */
  line-height: 0.5;    /* dng gn hn */
}


/* Responsive trn mobile, ch gim font/icon  va khung */
@media (max-width: 768px) {
  .name {
    font-size: 1.8rem;
  }

  .genderIcon {
    font-size: 1.5rem;
  }

  .personalInfo {
    gap: 4px;
  }
}