/* src/components/Portal/PortalPage.module.css */

.portalPageContainer {
  display: grid;
  grid-template-columns: 1fr auto;
  /* 1fr cho ni dung chnh, auto cho slider */
  max-width: 1600px;
  margin: 0 auto;
  /* Cn gia container */
  padding: 20px;
  box-sizing: border-box;
  gap: 20px;
  /* Khong cch gia cc ct */
}

/* Style cho vng ni dung chnh */
.portalContent {
  /* flex: 1; Xa dng ny */
  /* width: 100%; Xa dng ny */
  display: flex;
  /*  cn gia ni dung bn trong */
  flex-direction: column;
  /*  cc section xp chng ln nhau */
  align-items: center;
  /* Cn gia cc section theo chiu ngang */
}


.articlesTitle {
  margin-bottom: 20px;
  /* padding-left: 20px; Xa dng ny nu khng cn thit */
  font-size: 1.5em;
  font-weight: bold;
  width: 100%;
  /*  title chim ton b chiu rng */
  text-align: left;
  /* Cn tri tiu  */
}
/* Cc style cho responsive */
@media (max-width: 768px) {
  .portalPageContainer {
    grid-template-columns: 1fr;
    /* Chuyn sang 1 ct trn mn hnh nh */
    align-items: center;
    /* Cn gia cc phn t */
  }
}