.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
  overflow: auto; /* h tr cun nu modal ln */
}

.modal-content {
  background: white;
  font-weight: normal;
  text-align: left;
  padding: 20px;
  border-radius: 8px;
  position: relative;

  /* Chiu rng t ng theo ni dung */
  width: auto;
  min-width: 600px;
  max-height: 90vh;
  overflow-y: auto;
  overflow-x: auto; /* Cho php cun ngang nu ni dung rng */
}

.modal-close {
  position: absolute;
  top: 10px;
  right: 15px;
  border: none;
  background: none;
  font-size: 20px;
  cursor: pointer;
}
