/* src/components/Comics/Comics.module.css */

.wrapper {
  width: 100%;
  min-height: 100vh;
  padding-top: 0;
  background: #f1f3f4; /* nn xm nht ton khi */
  box-sizing: border-box;
}

.tabs {
  display: flex;
  justify-content: center;
  gap: 2px;
  padding: 8px 16px 0; /* padding-top + sides, b padding-bottom */
  background: #f1f3f4; /* nn xm nht ng b */
  overflow-x: auto;
  margin-bottom: 0;
}

.tab {
  padding: 12px 24px;
  margin-bottom: 0;
  font-weight: 600;
  color: #555;
  background: #f1f3f4; /* tab khng active cng xm nht */
  border: none;
  border-radius: 12px; /* bo trn 4 gc mc nh */
  cursor: pointer;
  flex-shrink: 0;
  transition: background-color 0.2s, color 0.2s, border-radius 0.3s ease;
}

.tab:not(.active):hover {
  background: #e5e7eb; /* hover vn xm nht m hn */
}

.tab.active {
  background: #fff; /* ch tab active mi trng  ni bt */
  color: #007bff;
  border-radius: 12px 12px 0 0;
  box-shadow: 0 -2px 6px rgba(0, 0, 0, 0.1);
}

.content {
  margin-top: 0;
  padding-top: 0;
  background: transparent; /* ni dung di trong sut, hin th nn xm ca wrapper */
}
