/* src/components/Portal/PComics.module.css */

.pcomicsContainer {
  /* Thm cc style cho container nu cn, v d: */
   text-align: center; 
}

.pcomicsList {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
  justify-content: center;
  /* Cn gia cc item trn dng */
}

.pcomicsItem {
  width: 200px;
  margin: 10px;
  text-align: center;
  position: relative;
  /* Cn  nh v tuyt i cho title */
}

.comicLink {
  text-decoration: none;
  color: #333;
  display: block;
  /* width: 100%;  // Th thm dng ny nu cn full width link*/
  /* height: 100%; //  Th thm dng ny nu cn full height link*/
}

.comicCover {
  width: 100%;
  aspect-ratio: 13/19;
  object-fit: cover;
  border-radius: 5px;
  transition: transform 0.3s ease;
  /* Gi li transition */
}

.comicCover:hover {
  transform: scale(1.05);
  /* Gi li hiu ng hover phng to */
}

.comicTitle {
  font-size: 16px;
  margin-top: 5px;
  text-align: center;
  color: #333;
  opacity: 0;
  /* n tiu  mc nh */
  transition: opacity 0.3s ease;
  /* Thm transition cho hiu ng m dn */
}

.pcomicsItem:hover .comicTitle {
  opacity: 1;
  /* Hin th tiu  khi hover vo item */
}