/* src/components/Portal/PCharacters.module.css */

.pcharactersContainer {
    margin-bottom: 20px;
}

.characterWrapper {
    display: flex;
    flex-direction: row;
    /* Mc nh l hng ngang */
    align-items: flex-start;
    /* Cn chnh theo chiu dc */
    border: 1px solid #ccc;
    padding: 10px;
}

/* Media query  chuyn sang ct khi mn hnh nh */
@media (max-width: 768px) {
    .characterWrapper {
        flex-direction: column;
        /* Chuyn sang ct */
    }
}

.characterListArea {
    width: 40%;
    /* Chim 40% chiu rng */
    display: flex;
    flex-direction: row;
    align-items: center;
}

.characterList {
    display: flex;
    flex-direction: row;
    overflow-x: auto;
    /* Thm thanh cun ngang nu cn */
    padding: 10px;
}

/* Style cho hnh trn avatar */
.characterItem {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    overflow: hidden;
    margin: 0 5px;
    cursor: pointer;
    transition: transform 0.2s ease-in-out;
}

.characterItem:hover {
    transform: scale(1.1);
}

.characterItem.selected {
    border: 2px solid blue;
    /* Hin th vin khi c chn */
}

.characterAvatar {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.characterDetailArea {
    width: 60%;
    /* Chim 60% chiu rng */
    padding: 10px;
    border-left: 1px solid #eee;
}

.characterName {
    font-size: 1.2em;
    margin-bottom: 5px;
}

.characterDescription {
    font-style: italic;
    color: #555;
}

.arrowButton {
    background-color: transparent;
    border: none;
    font-size: 1.5em;
    cursor: pointer;
    padding: 5px;
}

.arrowButton:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}