/* src/components/Portal/PArticles.module.css */

/* Ci t kch thc hnh nh (c th thay i) */
:root {
  --main-article-image-height: 300px;
  --middle-article-image-height: 150px;
  --side-article-image-height: 100px;
  --section-gap: 30px;
  /* Khong cch gia cc phn */

  /* Kch thc vng (c th iu chnh) */
  --main-article-width: 43%;
  --middle-articles-width: 25%;
  --side-articles-width: 32%;

  /* Kch thc ch (c th iu chnh) */
  --main-article-title-size: 1.6em;
  --middle-article-title-size: 1em;
  --side-article-title-size: 0.9em;
}

.articlesSection {
  position: relative;
  /*  nh v cc phn t gi */
  padding: 20px 0;
  /* Khong cch trn v di */
  margin-bottom: 30px;
  /* Thm khong cch di cng */
}

.articlesTitle {
  /*text-align: center;*/
  /* Loi b cn gia */
  margin-bottom: 20px;
  padding-left: 20px;
  /* Thm khong cch bn tri */
  font-size: 1.5em;
  /* iu chnh kch thc ch */
  font-weight: bold;
  /* Ch m */
}

.articlesContainer {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
  /* S dng margin thay v gap */
  margin-left: calc(var(--section-gap) * -1);
  /* B khong trng bn tri */
}

/* Main Article Styles */
.mainArticle {
  width: var(--main-article-width);
  box-sizing: border-box;
  padding-left: var(--section-gap);
  /* Khong cch bn tri */
}

.mainArticleImage {
  width: 100%;
  height: var(--main-article-image-height);
  object-fit: cover;
  aspect-ratio: 1 / 1;
  /* Hnh vung */
}

.mainArticle h3 {
  font-size: var(--main-article-title-size);
  margin: 5px 0;
}

.articleSummary {
  font-size: 0.9em;
  color: #555;
  margin-top: 5px;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;

  overflow: hidden;
}

.mainArticle .articleDivider {
  /* ng k gia title v summary */
  border-bottom: 1px dashed #ccc;
  margin: 10px 0;
}

/* Middle Articles Styles */
.middleArticles {
  width: var(--middle-articles-width);
  box-sizing: border-box;
  padding-left: var(--section-gap);
  /* Khong cch bn tri */
}

.middleArticle {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}

.middleArticleImage {
  width: 100%;
  height: var(--middle-article-image-height);
  object-fit: cover;
  aspect-ratio: 4 / 3;
}

.middleArticle h3 {
  font-size: var(--middle-article-title-size);
  margin: 5px 0;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  /* Gii hn 3 dng */
  line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-align: justify;
  /* Cn u */
  text-align-last: left;
  /* Dng cui cng cn tri */

  /* Thm thuc tnh tiu chun */
}

/* Side Articles Styles */
.sideArticles {
  width: var(--side-articles-width);
  box-sizing: border-box;
  padding-left: var(--section-gap);
  /* Khong cch bn tri */
}

.sideArticle {
  display: grid;
  /* S dng CSS Grid */
  grid-template-columns: 40% 60%;
  /* iu chnh t l (hnh:ch) */
  gap: 5px;
  /* Khong cch gia hnh v ch */
  margin-bottom: 10px;
  padding-bottom: 10px;
  border-bottom: 1px dashed #ccc;
  /* ng gch ngang */
  align-items: start;
  /* Cn ch ln trn */
}


/* Loi b ng gch ngang  bi vit cui cng */
.sideArticle:last-child {
  border-bottom: none;
}

.sideArticle h3 {
  font-size: var(--side-article-title-size);
  margin: 5px 0;
  margin-inline: 10px 15px;
  /* L tri 10px, l phi 15px */
  text-align: justify;
  /* Cn u */
  text-align-last: left;
  /* Dng cui cng cn tri */
  display: -webkit-box;
  -webkit-line-clamp: 3;
  /* Gii hn 2 dng */
  -webkit-box-orient: vertical;
  overflow: hidden;
  line-clamp: 3;
  /* Thm thuc tnh tiu chun */
}

.sideArticleImage {
  width: 100%;
  /* Tng chiu ngang hnh nh */
  height: auto;
  object-fit: cover;
  aspect-ratio: 3/2;
  margin-right: 10px;
}

/* n vng content ph trn mn hnh nh */
@media (max-width: 768px) {
  .sideArticles {
    display: none;
  }

  .mainArticle {
    width: 100%;
    padding-left: 0;
  }

  .middleArticles {
    width: 100%;
    padding-left: 0;
  }
}

/* Common Styles */
h3 {
  font-size: 1em;
  margin: 5px 0;
}

.viewMoreButton {
  display: block;
  margin-left: 25px;
  text-align: center;
  padding: 10px;
  background-color: #007bff;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  margin-top: 0px;
}

/* Hiu ng hover */
.articleLink {
  display: block;
  text-decoration: none;
  color: inherit;
  transition: color 0.2s ease;
  /* Thm hiu ng chuyn i */
}

.articleLink:hover {
  color: rgb(54, 84, 238);
  /* Mu  khi hover title */
}

.articleLink img {
  transition: opacity 0.2s ease;
}

.articleLink:hover img {
  opacity: 0.8;
  /* Hiu ng sng nh khi hover nh */
}