:global(.descriptionBlock) {
    margin: 16px 0;
  }
  


  .descriptionBlock {
    flex: 1;
    padding: 0;
    margin: 0;
    border-radius: 8px;
    /* Tr v normal  dng margin ca <p> */
    white-space: normal;
    font-size: 0.95rem;
    /* Bn c th gim nh line-height nu mun */
    line-height: 1.4;      
    text-align: left;
    word-break: break-word;
  }
  
  /* Gim margin bottom ca <p> cho gn nhau hn */
  .descriptionBlock p {
    margin: 0 0 0.1em;  /* trc y mc nh c th l 1em */
  }
  
  /* Cng m bo <br> khng to qu nhiu khong trng */
  .descriptionBlock br {
    display: block;
    content: "";
    margin: 0;           /* xa margin mc nh */
    line-height: 0.5;    /* dng gn hn */
  }
  
  